        "Critical Writing: net/web Art : The Work of Art in the age of the internet my choice of net/web art was Indirect Flights. Mr. Benjamin mentioned in his essay, The work of Art in the age of its mechanical reproduction that it is almost human nature to want to copy something so historically the idea of reproduction is not new.The authenticity of an art work is what Benjamin describes as the aura, a phenomenas uniqueness (Benjamin, 2015). His theory suggests that every original piece of art has an aura, for example, the aura of the sunset, the aura of nature (Benjamin, 2015) that cannot be replaced. Therefore mechanically or technologically reproduced art refuse to appreciate or acknowledge that aura. Benjamin thinks highly of old forms of art, he describes them as creativity and genius. This is because of the value and mystery embedded in them and reproduction undoes that appreciation or status. The Work Of Art In The Age Of The Internet acknowledges the aura that Benjamin talks about but suggests that art on the internet still has evidence of contemplative emersion that usually is lost in mechanically reproduced art. This is because when viewing art on the internet we still somehow find ourselves staring. This contemplative imersion is possible because the internet offers tools that allow art to be viewed in different new ways. Digital art pieces can be viewed with music to assist with immersion  just like my case study Indirect Flights. Another tool that the internet offers,  is the ability to view art so closely that you can see the smallest details, the display system has changed, you see the work better, close ups and etc. (Carrier, 2020). Art on the internet today has taken this to a whole different level. Danae mentioned that net art today can be seen as clunky, old fashioned or uncool. But I argue that, a piece like Indirect Flight, being able to interact with the artwork in new ways can be seen as a future improvement. The piece is layered with photos, a zooming in and out tool would have been so ideal. With that being said I still think the layers come together so nicely and I was still able to see and appreciate its beauty. REFERENCES- 1.W (1935). The Work of Art in the Age of Mechanical Reproduction. Translated by J. A. Underwood, Penguin Books (2008). 2.  D (2020). The Work of Art in the Age of the internet.3. Danae (2019). Net Art, Post-internet Art, New Aesthetics: The Fundamentals of Art on the Internet",
